.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
#body{
  display: flex;
  flex-direction: row;
  margin-top: 20px;
}
#filterbar{
  height: 280px;
  width: 272px;
  padding-left: 48px;
  overflow-y: hidden;
  border: solid 2px gray;
  /* padding: 10px; */
  margin-left: 10px;
  border-radius: 8px;
  margin-top: 40px;
  padding-top: 20px;
}
#filter{
  font-weight: 500;
  font-size: 16px;
  line-height: 1.5;
font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
}
.filter-value{
  width:50px;
}
#Product-category{
  display: flex;
  flex-direction: column;
  margin-left: 20px;
}
#price{
  display: flex;
  flex-direction: row;
}
#Submit{
  border:solid 1px gray;
  border-radius: 8px;
  margin-left: 5px;
  padding:3px;
}
.Product-card{
  display: flex;
  flex-direction: column;
}
.Product-details{
  display: flex;
  flex-direction: column;
  margin-top: 10px;
}
#Clear{
  border: solid 1px gray;
  width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 20px;
  border-radius: 8px;
  cursor: pointer;
}
#FootWear
{
border: solid 1px gray;
width:90px;
margin-bottom: 5px;
border-radius:8px;  
display: flex;
align-items: center;
justify-content: center;
cursor: pointer;

}
#TopWear{
  border: solid 1px gray;
  width:90px;
  margin-bottom: 5px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;

}
#BottomWear{
  border: solid 1px gray;
  width:90px;
  margin-bottom: 5px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;

}
#Main-body{
  display: grid;
  grid-template-columns: repeat(auto-fit,325px);
  width: 100%;
  grid-gap: 20px;
  padding: 40px;
  padding-left: 20px;


}
#grid-items{
  background-color:rgb(245, 245, 245);
  width:325px;
  height: 250px;
}
#break{
  border-bottom: solid 1px lightgray;
  margin-left: 0px;
  padding-left: 0px;
  margin-right: 73px;
  margin-top: 27px;
  margin-bottom: 18px;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
